.question {
  max-width: 320px;
  margin: 0 auto;
}

.question + .question {
  margin-top: 20px;
}

.quest_cont {
  padding: 0 16px;
}

.quest_content {
  border: 1px solid #b4f5ff;
  border-radius: 28px;
  padding: 24px 4px;
}

.type_title {
  text-align: center;
  margin-bottom: 12px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.4;
}

.quest_svg {
  width: 20px;
  height: 20px;
}

.qust_settings_list_item {
  gap: 10px;
  padding: 10px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
  display: flex;
}

.qust_settings_list_item + .qust_settings_list_item {
  margin-top: 4px;
}

.cont {
  width: 226px;
}

.max-mark {
  width: 57px;
  color: #0c0c0c;
  background-color: #fcffe9;
  border: 1px solid #fec204;
  border-radius: 12px;
  padding: 16px 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.4;
}

.max-mark::-ms-input-placeholder {
  width: 41px;
  text-align: center;
  color: #0c0c0c;
}

.max-mark::placeholder {
  width: 41px;
  text-align: center;
  color: #0c0c0c;
}

.quest {
  width: 100%;
  border: 1px solid #b4f5ff;
  border-radius: 12px;
  margin-bottom: 20px;
  padding: 8px 4px;
}

.answ_list_item {
  align-items: center;
  gap: 20px;
  padding: 7px 0;
  display: flex;
}

.more-svg {
  display: none;
}

.ans {
  width: 100%;
  color: #8c8fa4;
  border-bottom: 1px solid #0c0c0c;
  padding: 8px;
}

.answ_list_item + .answ_list_item {
  margin-top: 4px;
}

.pad {
  display: none;
}

.settings {
  margin-bottom: 20px;
}

.denger {
  border: 4px solid #ffa8a8;
}

.btn_control + .btn_control, .btn_list_quest {
  margin-top: 20px;
}

.correct {
  background-color: #f6fff3;
  border-bottom: 1px solid #58d513;
  border-radius: 12px;
}

.ans_input {
  border-bottom: 1px solid #0c0c0c;
  padding: 12px 8px;
  display: flex;
}

.answe_text {
  width: 100%;
  border: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.4;
}

@media screen and (min-width: 768px) {
  .question {
    max-width: 768px;
    margin: 0 auto;
  }

  .quest_cont {
    padding: 0 24px;
  }

  .quest_content {
    padding: 20px 8px;
  }

  .question + .question {
    margin-top: 40px;
  }

  .type_title {
    text-align: left;
    margin-bottom: 24px;
  }

  .quest_svg {
    width: 24px;
    height: 24px;
  }

  .qust_settings_list_item {
    align-items: center;
    gap: 12px;
    padding: 8px;
    display: flex;
  }

  .cont {
    width: 423px;
  }

  .mod {
    display: none;
  }

  .pad {
    display: block;
  }

  .input_quest {
    border: 1px solid #b4f5ff;
    border-radius: 8px;
    gap: 20px;
    margin-bottom: 24px;
    padding: 12px 16px;
    display: flex;
  }

  .quest {
    width: 100%;
    border: none;
    border-radius: 12px;
    margin-bottom: 0;
    padding: 0;
    font-size: 16px;
    font-weight: 500;
  }

  .quest::-ms-input-placeholder {
    color: #0c0c0c;
  }

  .quest::placeholder {
    color: #0c0c0c;
  }

  .max-mark {
    width: 78px;
    padding: 8px 12px;
    font-size: 16px;
  }

  .max-mark::-ms-input-placeholder {
    width: 54px;
    text-align: center;
    color: #0c0c0c;
  }

  .max-mark::placeholder {
    width: 54px;
    text-align: center;
    color: #0c0c0c;
  }

  .more-svg {
    width: 24px;
    height: 24px;
    display: block;
  }

  .ans {
    justify-content: space-between;
    align-items: center;
    padding: 12px 8px;
    display: flex;
  }

  .answ_list_item + .answ_list_item {
    margin-top: 20px;
  }

  .settings {
    margin-bottom: 40px;
  }

  .btn_control + .btn_control {
    margin-top: 0;
  }

  .btn_list_quest {
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
    display: flex;
  }
}

@media screen and (min-width: 1440px) {
  .question {
    max-width: 940px;
    margin: 0 auto;
  }

  .quest_cont {
    padding: 0 20px;
  }

  .quest_content {
    padding: 20px;
  }

  .qust_settings_list_item {
    align-items: center;
    display: flex;
  }

  .qust_settings_list {
    flex-wrap: wrap;
    gap: 24px;
    display: flex;
  }

  .quest::-ms-input-placeholder {
    color: #8c8fa4;
  }

  .quest::placeholder {
    color: #8c8fa4;
  }

  .question + .question {
    margin-top: 60px;
  }
}

